import { Observable } from "rxjs";
import { Injectable } from "@angular/core";

import { HttpClient, HttpParams } from "@angular/common/http";
import { environment } from "@env/environment";
import {
  RateDto
} from "../model/playing-field";

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: "root"
})
export class RateService {
  constructor(private http: HttpClient) {}

  getRate(id) {
    return this.http.get<RateDto>(`${API_URL}/playingField/${id}/getRate`);
  }

  addRate(id, rate): Observable<number> {
    let formData = new FormData();
    formData.append("rate", rate);
    return this.http.post<number>(
      `${API_URL}/playingField/${id}/addVote`,
      formData
    );
  }

  existsRateByLoggedUser(id): Observable<boolean> {
    return this.http.get<boolean>(`${API_URL}/playingField/${id}/existsRate`);

  }
}
